@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=MuseoModerno:wght@200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:wght@200&display=swap');

body {
    background-color: #dfeaec;
    margin: 0;
    padding: 0;
}

.containerMain .image {
    position: absolute;
    margin-left: 1050px;
}

.containerMain .main-content {
    position: absolute;
    margin-left: 500px;
    margin-top: 40px;
}

.containerMain .main-content h1{
    font-family: 'Poppins', sans-serif;
    font-size: 4rem;
    color: #a86302;
    text-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #fff;
}

.containerMain .main-content p{
    margin-top: -30px;
    font-family: 'Poppins', sans-serif;
    color: #393939;
    font-weight: 500;
    font-size: 2.25rem;
    text-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #fff;
}

.containerMain .main-content h4{
    box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #fff;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    color: white;
    border-radius: 10px;
    background-color: #a86302;
    padding-top: 15px;
    padding-right: 19px;
    padding-left: 19px;
    padding-bottom: 15px;
    position: absolute;
    cursor: pointer;
}

.containerMain .main-content h4:hover {
    transition: 1s;
    background-color: white;
    color: #a86302;
}