@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=MuseoModerno:wght@200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:wght@200&display=swap');

body{
    margin: 0;
    padding: 0;
}

.containerNav {
    margin-top: -17px;
    z-index: 2;
    position: fixed;
    padding-top: 5px;
    width: 100%;
    height: 70px;
    background-color: #dfeaec;
    box-shadow: 3px 3px 4px gray;
}

.containerNav .image {
    margin-top: 5px;
    margin-left: 500px;
}


.containerNav .Navbar ul{
    position: absolute;
    margin-top: -50px;
    display: flex;
    list-style: none;
    margin-left: 1010px;
}

.containerNav .Navbar ul li a{
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    text-decoration: none;
    margin-left: 50px;
    font-weight: 500;
    color: #4a4a4a;
}

.containerNav .Navbar ul li a:hover{
    color: #a86302;
    transition: 1s;
}